import { useQuery, useQueryClient } from "@tanstack/react-query"
import { getMetadata } from "../services/getMetadata"
import { filesQueryKeys } from "../utils"

const useFile = (id: string | undefined) => {
  const queryClient = useQueryClient()

  return useQuery({
    queryKey: filesQueryKeys.metadata(id),
    queryFn: () => getMetadata(id, queryClient),
    enabled: !!id,
    staleTime: 60 * 1000,
    refetchOnWindowFocus: false,
  })
}

export { useFile }
